.domino {
    width: 50px;
    height: 100px;
    background-color: #ffffff;
    border: 1px solid #000;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: #000;
}

.domino:hover {
    box-shadow: 0 0 10px rgb(88, 93, 236);
    border: 1.5px solid #000;
    border-radius: 6px;
    width: 48px;
    height: 98px;
}

.domino:active {
    box-shadow: 0 0 10px rgb(88, 93, 236);

}

.domino.selected {
    background-color: #ffffff;
    margin-top: 0px;
    border: 2px solid #000;
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease;
    width: 30px;
    height: 75px;
}

.topnumber,
.bottomnumber {
    text-shadow: 0.5px 0.5px 1px #000;
    flex-direction: column;
    width: 100%;
    text-align: center;
    justify-content: center;
    padding: 1vw 0 1vw 0;
    font-size: 125%;

}

.line {
    width: 100%;
    border-top: 1px solid #000;
}

.dominoImageTop {
    width: 40px;
    height: auto;
    padding-bottom: 5px;
}

.dominoImageBottom {
    width: 40px;
    height: auto;
    padding-top: 5px;
}